import React, { useContext, useState } from "react";
import { GlobalStateContext } from "../contextRedu/GlobalState";
import {
  ListGroup,
  ListGroupItem,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import { CartItem } from "./CartItem";

export const Cart = () => {
  const { cartItems, onAdd, decrementCount, onRemove } = useContext(GlobalStateContext);
  const itemsPrice = cartItems.reduce((a, b) => a + b.price * b.qty, 0);
  const tax = itemsPrice * 0.14;
  const shippingFee = itemsPrice > 100 ? 0 : 50;
  const total = itemsPrice + tax + shippingFee;

  // const { buttonLabel, className } = props;

  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);
  return (
    <div className="container-fluid">
      <h3> Your Cart</h3>
      <ListGroup>
        {cartItems.map((cartItem) => {
          return (
            <ListGroupItem key={Math.random(Math.floor() * 100)}>
              <CartItem
                cartItemName={cartItem.name}
                cartItemPrice={cartItem.price}
                cartItemImg={cartItem.img}
                cartItemsQty={cartItem.qty}
                cartItemsid={cartItem.id}
                onAdd={onAdd}
                decrementCount={decrementCount}
                cart={cartItem}
                onRemove={onRemove}
              />
            </ListGroupItem>
          );
        })}
      </ListGroup>
      {cartItems.length !== 0 ? (
        <>
          <hr style={{ height: "3px", backgroundColor: "red" }} />
          <p style={{ fontWeight: "500" }}>
            {"Item Total Price : $ " + itemsPrice} <br />
            {"Tax : $ " + tax.toFixed(2)} <br />
            {"Shipping Fee : $ " + shippingFee}
            <br />
            <span className="text-muted fst-italic">
              (free shipping on Min. spend of $100)
            </span>
            <br />
            <span className="fs-1 text-success">
              {"Total : $ " + total.toFixed(2)}
            </span>
          </p>
          <div>
            <Button color="primary" onClick={toggle}>
              Check Out
            </Button>
            <Modal isOpen={modal} toggle={toggle}>
              <ModalHeader toggle={toggle}>Success!!</ModalHeader>
              <ModalBody>
               Your Order Has Placed!
               Thanks for Shopping
              </ModalBody>
              <ModalFooter>
                <Button color="secondary" onClick={toggle}>
                 Exit
                </Button>
              </ModalFooter>
            </Modal>
          </div>
        </>
      ) : (
        <span>Place Order</span>
      )}
    </div>
  );
};
