import React, { createContext, useReducer } from "react";
import Reducer from "./Reducer";

// initial state
const initialState = {
  products: [
    {
      id: 1,
      name: "Shoes",
      price: 10,
      desc: "Short description here",
      img:
        "https://assets.adidas.com/images/w_600,f_auto,q_auto/449c838942da409f8ba9a97f00d3cffe_9366/Runfalcon_Shoes_Black_F36199_01_standard.jpg",
    },
    {
      id: 2,
      name: "Slippers",
      price: 15,
      desc: "Short description here",
      img:
        "https://assets.adidas.com/images/w_600,f_auto,q_auto/89289a3dbb1a4c0dbfe3a9910019a415_9366/Adilette_Aqua_Slides_Red_F35540.jpg",
    },
    {
      id: 4,
      name: "Cap",
      price: 13,
      desc: "Short description here",
      img:
        "https://assets.adidas.com/images/w_600,f_auto,q_auto/62ad810c54cc402f8707aa9a00b92ec7_9366/Cap_Pink_FN0989.jpg",
    },
  ],
  cartItems: [
  ],
};

//id: 12, name: "shoes", price: 12, desc: '', image: "path"

// declare context
export const GlobalStateContext = createContext(initialState);

// assign globalState Provider Value
export const GlobalProvider = ({ children }) => {
  // declare Reducer
  const [state, dispatch] = useReducer(Reducer, initialState);

  // Reducer Functions
  const decrementCount = (value) => {
    dispatch({
      type: "decrement",
      payload: value
    });
  };
  const onAdd = (val) => {
    dispatch({
      type: "onAdd",
      payload: val,
    });
  };

  const onRemove = (val) =>{
    dispatch({
      type: "onRemove",
      payload: val,
    });
  }
  return (
    <GlobalStateContext.Provider
      value={{
        products: state.products,
        cartItems: state.cartItems,
        decrementCount,
        onAdd,
        onRemove
      }}
    >
      {children}
    </GlobalStateContext.Provider>
  );
};
