import React, { useContext } from 'react'
import { Navbar, NavItem, Nav, NavbarBrand} from 'reactstrap'
import { GlobalStateContext } from "../contextRedu/GlobalState";

export const Header = () => {
  const { cartItems } = useContext(GlobalStateContext);
    return (
        <>
          <Navbar className="bg-info text-white">
              <NavbarBrand className="text-white">
                MYSHOPPINGCART
              </NavbarBrand>
              <Nav>
                  <NavItem className="text-white">
                   Cart
                    <span className="badge bg-danger mr-3 ml-2 ">{cartItems.length}</span>
                  </NavItem>
                  <NavItem className="text-white">
                    <span>Sign In</span>
                  </NavItem>
              </Nav>
          </Navbar>
        </>
    )
}
