import React from "react";

export const CartItem = (props) => {
  const {
    onRemove,
    cartItemImg,
    cartItemName,
    cartItemPrice,
    cartItemsQty,
    cart,
    onAdd,
    decrementCount,
  } = props;
  const subtotal = cartItemsQty * cartItemPrice;
  return (
    <div className="container-fluid p-0 d-flex justify-content-between">
      <div className="d-flex">
        <img src={cartItemImg} style={{ maxWidth: "100px" }}></img>
        <div className="mx-2">
          <h4>{cartItemName}</h4>
          <strong className="text-secondary">$ {cartItemPrice}</strong>
          <div className="d-flex mt-2">
            <button className="btn btn-warning" onClick={() => onAdd(cart)}>
              +
            </button>
            <button
              className="btn btn-danger ml-2"
              onClick={() => decrementCount(cart)}
            >
              -
            </button>
          </div>
          <strong>
            {cartItemsQty +
              " x " +
              " $" +
              cartItemPrice +
              " = " +
              "$" +
              subtotal}
          </strong>
        </div>
      </div>

      <button className="btn btn-secondary txt-light" onClick={() => onRemove(cart)}>X</button>
    </div>
  );
};
