import React, { useContext } from "react";
import { ProductItem } from "./ProductItem";
import { GlobalStateContext } from "../contextRedu/GlobalState";

export const Products = () => {
  const { products, onAdd,  cartItems, setCartItems } = useContext(GlobalStateContext);

  return (
    <>
      <h1>Products</h1>
      <div className="container-fluid">
        <ul className="row" style={{ listStyleType: "none", padding: "0" }}>
          {products.map((product) => (
            <li className="col-sm-4 p-2" key={product.id}>
              <ProductItem
                onAdd={onAdd}
                description={product.desc}
                productName={product.name}
                price={product.price}
                img={product.img}
                products={product}
                cartItem={cartItems}
                setCartItems={setCartItems}
              ></ProductItem>
            </li>
          ))}
        </ul>
      </div>
    </>
  );
};
