export default (state, action) => {
  switch (action.type) {
    case "decrement":
      const cart = action.payload;
      const exist = state.cartItems.find((x) => x.id === cart.id);

      return {
        ...state,
        cartItems:
          exist.qty === 1
            ? state.cartItems.filter((x) => x.id !== cart.id)
            : state.cartItems.map((x) =>
                x.id === cart.id ? { ...exist, qty: exist.qty - 1 } : x
              ),
      };

    case "onAdd":
      const cartItem = action.payload;
      const selected = state.cartItems.find((x) => x.id === cartItem.id);

      return {
        ...state,
        cartItems: selected
          ? state.cartItems.map((x) =>
              x.id === cartItem.id ? { ...selected, qty: selected.qty++ } : x
            )
          : [...state.cartItems, { ...cartItem, qty: 1 }],
      };

    case "onRemove":
      return {
        ...state,
        cartItems: state.cartItems.filter((x) => x.id !== action.payload.id),
      };
    default:
      return state;
  }
};
